$font-stack: Tahoma, sans-serif;
$primary-color: #133625;
$bg:#e6cc96;

body {
  font: 100% $font-stack;
  color: $primary-color;
  background: $bg;
}
#menunav li{
	ul.dropdown-menu{
		background: #d7f2ba;
	}
	ul.dropdown-menu a:link{
		color: #198754;
	}
	ul.dropdown-menu a:hover{
		background: #f6e4b8;
	}
}
/************Bootstrap**********************/
#top-section{
	.form-control:focus {
  		border-color: #104E09 !important;
  		box-shadow: 0 5px 15px rgba(0,0,0,0.3) !important;
	}
}
/*******************************Carousel*******************************/
.carousel-fade .carousel-inner{
	
	.item {
	  opacity: 0;
	  -webkit-transition-property: opacity;
	  -moz-transition-property: opacity;
	  -o-transition-property: opacity;
	  transition-property: opacity;
	}
	.active {
	  opacity: 1;
	}
	.active.left,
	.active.right {
	  left: 0;
	  opacity: 0;
	  z-index: 1;
	}
	.next.left,
	.prev.right {
	  opacity: 1;
	}
	z-index: 2;
}

